
.auth {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 40px 0;
    align-items: center;
    justify-content: center;
    margin: auto;
}
.authContent {
    display: flex;
    align-items: center;
}
.oauth2Discord{
    z-index: 2;
    background: #7289DA;
    padding: 0 20px;
    margin-left: 10px;
    line-height: 35px;
    color: #fff;
    font-size: 20px;
    display: block;
    text-decoration: none;
    border-radius: 3px;
    height: 40px;
}
.errorCard {
    z-index: 2;
    background: white;
    border-radius: 3px;
    border: 1px solid #7289DA;
    padding: 4px 2px;
    display: flex;
    width: 70%;
    box-shadow: 0 2px 4px rgb(113, 136, 217);
}
.errorCard .title {
    flex-basis: 30%;
    border-right: 1px dashed #7289DA;
    display: flex;
}
.errorCard h3{
    color: #7188d9;
    margin: 0 10px;
}
.errorCard p {
    line-height: 25px;
    margin:  0 10px;
    color: #bababa;
    flex-basis: 70%;
}